import React, { useEffect, useState } from "react";
import { notification } from "antd";

import { VideoSlide } from "components/common";

const overVideoText = {
  color: "white",
  fontWeight: "400",
};

const openNotification = () => {
  notification.open({
    message: <h3>Hey!</h3>,
    description: (
      <p>
        This website is still under <b>heavy construction</b>. Most pages might
        be inexistent or far form their final look. In the meanwhile you can
        have a look at our <b>social pages</b>!
      </p>
    ),
    placement: "bottomRight",
    duration: 4.5,
  });
};

const Home = ({}) => {
  useEffect(() => {
    openNotification();
  }, []);

  return (
    <>
      <VideoSlide src="img/intro.mp4" first>
        <h2 style={overVideoText}>Welcome to</h2>
        <h1 style={overVideoText}>Weird Frames</h1>
      </VideoSlide>
      <div className="slide">{/*<h1>Prova</h1>*/}</div>
    </>
  );
};

export default Home;
