import React from "react";
import { Link } from "react-router-dom";
import { Button, Card, Col, Space } from "antd";
import {
  LinkOutlined,
  YoutubeOutlined,
  InstagramOutlined,
  FacebookOutlined,
} from "@ant-design/icons";

const { Meta } = Card;

const ConditionalButton = ({
  url,
  icon = <LinkOutlined />,
  isLink = false,
}) => {
  if (!isLink) return url ? <a href={url}>{icon}</a> : null;
  else return url ? <Link to={url}>{icon} Read More</Link> : null;
};

const ProjectCard = ({ title, description, img, link, yt, ig }) => {
  const actionsBlock = [];

  if (link) actionsBlock.push(<ConditionalButton url={link} isLink={true} />);
  if (yt)
    actionsBlock.push(
      <ConditionalButton url={yt} icon={<YoutubeOutlined />} />
    );
  if (ig)
    actionsBlock.push(
      <ConditionalButton url={ig} icon={<InstagramOutlined />} />
    );

  return (
    <Col sm={{ span: 24 }} md={{ span: 12 }} lg={{ span: 8 }}>
      <Card
        hoverable
        cover={<img alt={img.alt} src={img.src} />}
        actions={actionsBlock}
      >
        <Meta
          title={<h3 style={{ whiteSpace: "initial" }}>{title}</h3>}
          description={description}
        />
      </Card>
    </Col>
  );
};

export default ProjectCard;
