import React, { useEffect, useState } from "react";

const videoContainer = {
  position: "absolute",
  width: "100%",
  height: "100vh",
  overflow: "hidden",
  background: "black",
};

const player = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
};

const overVideoContent = {
  zIndex: 99999,
  textAlign: "center",
};

const VideoSlide = ({ src, children, first = false }) => {
  const [width, setWidth] = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);

  useEffect(() => {
    const handleResizeWindow = () => {
      setWidth(window.innerWidth);
      setHeight(window.innerHeight);
    };
    // subscribe to window resize event "onComponentDidMount"
    window.addEventListener("resize", handleResizeWindow);
    return () => {
      // unsubscribe "onComponentDestroy"
      window.removeEventListener("resize", handleResizeWindow);
    };
  }, []);

  let playerStyle = {};

  playerStyle["height"] = height + "px";

  let currWidth = (height / 9) * 16;
  if (currWidth < width) {
    let factor = (width * 9) / (height * 16);
    playerStyle["height"] = Math.ceil(height * factor) + 1 + "px";
  }

  const heightFirst = { height: first ? "calc(100vh - 90px)" : "100vh" };

  return (
    <>
      <div style={{...videoContainer, ...heightFirst}}>
        <video autoPlay muted style={{ ...playerStyle, ...player }}>
          <source src={src} type="video/mp4" />
        </video>
      </div>
      <div className="slide slide-first">
        <div style={overVideoContent}>{children}</div>
      </div>
    </>
  );
};

export default VideoSlide;
